<template>
    <div id="loading"> 
        <span>
             Loading...
        </span>
       
    </div>
</template>
<script>
export default {
    name:"load-module",
    // props:['loadboole'],
    data() {
        return {
            
        }
    },
    methods: {
        loadImages(urlArr){
        if(urlArr.length <= 0){
            return
        }
        let i = 0,
        timer = null,
        len = urlArr.length,
        load = (url) =>{
          if(i<len){
            const image = new Image()
            image.src = url 
            timer = setInterval(()=>{
              if(image.complete){
                clearInterval(timer)
                load(urlArr[i++])
              }
            },80)
          }else{
            this.loadboole = false
          }
        }
        load(urlArr[i])
      },
      tsts(){
        console.log("我是子组件")
      },
    },
    created() {
        
    }
}
</script>
<style lang="less" scoped>
    #loading{
        position: fixed;
        width: 100vw;
        height: 100vh;
        background: #333333;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 100;
    }
</style>