<template>
    <div>
        <div id="infoicon">
        <ul>
            <li @click="lertfun('music')"><span class="iconfont icon-vynil"></span></li>
            <li @click="lertfun('qq')" ><span class="iconfont icon-qq"></span></li>
            <li @click="lertfun('wx')"><span class="iconfont icon-gongzhonghao"></span></li>
            <li @click="lertfun('git')"><span class="iconfont icon-github"></span></li>
            <li @click="lertfun('fenxiang')"><span class="iconfont icon-fenxiang"></span></li>
            <li><span class="iconfont icon-photo"></span></li>
            <li class="toproll" @click="getfun" v-show="topboole"><span class="iconfont icon-huojian"></span></li>
        </ul>
        
    </div>
    <lert-module :hint="hint"></lert-module>
    <music-module v-show="musiclert"></music-module>
    </div>
    
</template>

<script>
import LertModule from "@/components/lertmodule/lertmodule.vue";
import MusicModule from "@/components/Music/Music.vue";
export default {
    name:"infoincon",
    data() {
        return {
            qq:"http://wpa.qq.com/msgrd?v=3&uin=1599731778&site=qq&menu=yes",
            git:"https://github.com/inoryEnglish/inory",
            topboole:false,
            hint:{
                title:"Inory remind you",
                content:"There is no information at this time",
                lertboole:false
            },
            lertbloor:false,
            musiclert:false,
            musicscale:false,
        }
    },
    methods: {
        musicfun(){
            this.musiclert = !this.musiclert
        },
        lertfun(value){
            switch (value) {
                case 'qq':
                    window.location.href = this.qq
                    break;
                case 'git':
                    window.location.href = this.git
                    break;
                case 'wx':
                    this.lertevent("温馨提示","当前博主暂时还不想公开微信，还请谅解。")
                    break;
                case 'fenxiang':
                    this.lertevent("温馨提示",`请您自己手动复制链接：${window.location.href}`)
                    break;
                case 'music':
                    this.musicfun()
                    break;
                default:
                    break;
            }
        },
        toprllfun(){
            if(this.topboole === true){
                window.requestAnimationFrame(function(){
                document.documentElement.scrollTop -= 20
            })
                this.getfun()
                if(document.documentElement.scrollTop===0){
                    this.topboole = false
                }
            }else{
                this.topboole = false
            }          
        },
        getfun(){
            this.topboole = true
            window.requestAnimationFrame(this.toprllfun)
        },
        scrollevent(){
            if(document.documentElement.scrollTop===0){
                this.topboole = false
            }else{
                this.topboole = true
            }
        },
        lertevent(a,b){
            this.hint = {
                title:a,
                content:b,
                lertboole:true
            }
            // this.lertbloor = true
        },
        
    },
    components:{
        LertModule,
        MusicModule
    },
    mounted(){
        window.addEventListener("scroll", this.scrollevent, true); 
    }
    
}

</script> 

<style lang="less" scoped>

#infoicon{
    position: fixed;
    z-index: 10;
    right: 0;
    top: 30px;
    transition: all .5s;
    animation: myinfo1 .5s;
    .toproll{
        position: fixed;
        right: 0px;
        bottom: 30px;
        transition: all 1s;
        box-shadow: inset 0px 0px 0px 4px black;
        span{
            font-size: 30px;
        }   
    }
    .toproll:hover{
        animation: huojian 1s infinite;
        span{
          color: red;   
        } 
    }
    ul{
        display: flex;
        justify-content: center;
        align-items: center;
        // background: rgba(0, 0, 0, 0.1);
        width: auto;
        li{
            margin: 0 10px;
            cursor: pointer;
            list-style: none;
            span{
                font-size: 22px;
                color: #51ff0c;
            }   
        }
    }
}
@media screen and (max-width: 570px) {
        #infoicon{
            animation: myinfo .5s;
            top: auto;
            bottom: 0;       
        }
}
@keyframes myinfo {
    to{
        transform: rotateY(0)
    }
    from{
        transform: rotateY(180deg)
    }
}
@keyframes myinfo1 {
    to{
        transform: rotateY(0)
    }
    from{
        transform: rotateY(180deg)
    }
}
@keyframes huojian {
    to{
        transform: scale(1);
    }
    from{
        transform: scale(1.4);
    }
}
@keyframes huojian1 {
    to{
        transform: rotate(-30deg);
    }
    from{
        transform: rotate(30deg);
    }
}
</style>