<template>
    <div id="music">
        <lert-module :hint="hint"></lert-module>
        <div class="musicbody" @click="serchclick('0')">  
            <div class="musicheader">
                <!-- 头部 -->
                <div>
                    <span class="iconfont icon-vynil"></span>
                    <cite>网易云音乐INoRy</cite>
                </div>
                <div>
                    <span class="iconfont icon-zuoyou" @click="lertfun('underdevelopment')"></span>
                    <span class="iconfont icon-you" @click="lertfun('underdevelopment')"></span>
                </div>
                <div>
                    <input type="text" placeholder="搜索单曲" @click.stop="serchclick('1')">
                    <span class="iconfont icon-sousuo"></span>
                    <!-- <div class="lertsearch" v-show="searchshow">
                        <div>

                        </div>
                        <div>
                            <h3>推荐搜索</h3>
                            <ul>
                                <li>童话 - 光良</li>
                                <li>童话 - 光良</li>
                                <li>童话 - 光良</li>
                                <li>童话 - 光良</li>
                            </ul>
                        </div>
                    </div> -->
                </div>
                <div>
                    <img src="http://p4.music.126.net/sSxqJmX8e60oeLS37UlBtg==/18959978509851854.jpg?param=30y30" alt="INORY">
                    <cite>inory</cite>
                    <span class="iconfont icon-triangle"></span>
                </div>
                <div>
                    <span class="iconfont icon-pifu"></span>
                    <span class="iconfont icon-xinxi"></span>
                    <span class="iconfont icon-shezhi"></span>
                </div>
                <div>
                    <span class="iconfont icon-suoxiao"></span>
                    <span class="iconfont icon-zuixiaohua"></span>
                    <span class="iconfont icon-zuidahua"></span>
                    <span class="iconfont icon-guanbi"></span>
                </div>
            </div>
            <div class="player">
                <!-- 音乐主题 -->
                <div class="left">
                    <div>
                        <img :src="playermessage.imgurl" alt="" srcset="" class="imganimtion" :class="{imganimtionstate:IsPlay}">
                    </div>
                    <div>
                        <ul>
                            <li>
                                <cite>喜欢</cite>
                                <span class="iconfont icon-xihuan"></span>
                            </li>
                            <li>
                                <cite>收藏</cite>
                                <span class="iconfont icon-shoucang"></span>
                            </li>
                            <li>
                                <cite>下载</cite>
                                <span class="iconfont icon-xiazai"></span>
                            </li>
                            <li>
                                <cite>分享</cite>
                                <span class="iconfont icon-fenxiang-copy-copy"></span>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="right">
                    <h3>{{playermessage.song}}</h3>
                    <div>
                        专辑：
                        <span>{{playermessage.album}}</span>
                        歌手：
                        <span>{{playermessage.singer}}</span>
                    </div>
                    <ul>
                        <li v-for="(item,index) in playermessage.arr" :key="index" :class="ishighight===index? 'hightcolor':'' ">
                            {{item}}
                        </li>
                    </ul>
                </div>
            </div>
            <div class="footermusic">
                <!-- 底部 -->
                <div>
                    <span class="iconfont icon-shangyishou" @click="leftevent"></span>
                    <span class="iconfont " :class="[IsPlay?'icon-zantingtingzhi':'icon-bofang']" @click="tablesong"></span>
                    <span class="iconfont icon-xiayishou" @click="rightevent"></span>
                </div>
                <div>
                    <span>{{playermessage.timestar}}</span>
                    <input type="range" min="0" :max="playermessage.max" ref="timerange">
                    <span>{{playermessage.timeend}}</span>
                </div>
                <div>
                    <span class="iconfont icon-yinliang"></span>
                    <input type="range" min="0" max="100" ref="volumeevent">
                </div>
                <div>
                    <span class="iconfont " @click="Playtheway" :class="Playthewaytab"></span>
                    <span class="iconfont icon-ci"></span>
                    <span class="iconfont icon-plist" @click="lertfun('plist')"></span>
                    <div class="songlist" v-show="songlistshow">
                        <ul>
                            <li v-for="(items,index) in detail" :key="items.index" @click="songplayer(`${items.id}`,`${index}`)">{{items.name}} - {{items.ar[0].name}}</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        
        <audio :src="playermessage.url" ref="audioref"> 
        </audio>
    </div>
</template>

<script>
import LertModule from "@/components/lertmodule/lertmodule.vue";
import axios from "axios"
export default {
    name:"music-module",
    data() {
        return {
            // Ismusicscale:false
            hint:{
                title:"Inory remind you",
                content:"There is no information at this time",
                lertboole:false
            },
            searchshow:false,
            songlistshow:false,
            songlist:[],
            detail:[],
            playermessage:{
                song:"",
                singer:"",
                lyric:"",
                time:"",
                url:"",
                imgurl:"",
                album:"",
                timestar:"00:00",
                timeend:"",
                max:"",
                id:""
            },
            IsPlay:false,
            ishighight:0,
            Playthewaynum:0,
            Playthewaytab:"icon-shunxubofang"
        }
    },
    computed: {
        
    },
    components:{
        LertModule
    },
    created(){
        this.songlistdata()
    },
    mounted(){
        this.timestar()
        this.oninputevent()
        this.volumefun()
        this.songplayer(535053308,72)
    },
    methods: {
        Playthewayfun(val){
            switch (val) {
                case 'suijibofang':
                    this.Playthewaytab = "icon-bofangye-caozuolan-suijibofang"
                    this.$refs.audioref.onended = ()=>{
                        this.songplayer(this.detail[parseInt(Math.random()*(this.detail.length))].id,parseInt(Math.random()*(this.detail.length)))
                    } 
                    break;
                case 'liebiaoxunhuan':
                    this.Playthewaytab = "icon-liebiaoxunhuan"
                    break;
                case 'danquxunhuan':
                    this.Playthewaytab="icon-danquxunhuan"
                    this.$refs.audioref.onended = ()=>{
                        this.playersong()
                    }
                    break;
                case 'shunxubofang':
                    this.Playthewaytab="icon-shunxubofang"
                    this.$refs.audioref.onended = ()=>{
                        this.rightevent()
                    }
                    break;
                default:
                    break;
            }
        },
        Playtheway(){
            // 播放方式 随机播放，非无限循环顺序播放，单曲循环，默认列表顺序无限循环
            this.Playthewaynum += 1;
            if(this.Playthewaynum === 4){
                this.Playthewaynum = 0;
            }
            switch (this.Playthewaynum) {
                case 1:
                    this.Playthewayfun('suijibofang')
                    
                    break;
                case 2:
                    this.Playthewayfun('liebiaoxunhuan')
                    break;
                case 3:
                    this.Playthewayfun('danquxunhuan')                  
                    break;
                case 0:
                    this.Playthewayfun('shunxubofang')                
                    break;
                default:
                    break;
            }
        },
        volumefun(){
            // 音量调整
            this.$refs.volumeevent.oninput = ()=>{
                this.$refs.audioref.volume = this.$refs.volumeevent.value*0.01
            }
        },
        rightevent(){//下一首
            if(this.Playthewaytab === "icon-bofangye-caozuolan-suijibofang"){
                this.songplayer(this.detail[parseInt(Math.random()*(this.detail.length))].id,parseInt(Math.random()*(this.detail.length)))   
            }else{
                if(parseInt(this.playermessage.id)+1===this.detail.length||parseInt((this.playermessage.id))+1===this.detail.length){
                    this.songplayer(this.detail[0].id,0)
                    
                }else{
                    this.songplayer(this.detail[parseInt(this.playermessage.id)+1].id,parseInt((this.playermessage.id))+1)
                }
                
            }
            
        },
        leftevent(){//上一首
            if(this.Playthewaytab === "icon-bofangye-caozuolan-suijibofang"){
                this.songplayer(this.detail[parseInt(Math.random()*(this.detail.length))].id,parseInt(Math.random()*(this.detail.length)))
            }else{
                if(parseInt(this.playermessage.id)-1===-1||parseInt((this.playermessage.id))-1===-1){
                    this.songplayer(this.detail[this.detail.length-1].id,this.detail.length-1)
                }else{
                    this.songplayer(this.detail[parseInt(this.playermessage.id)-1].id,parseInt((this.playermessage.id))-1)
                }
            }
        },
        tablesong(){
            this.IsPlay = !this.IsPlay
            if(!this.IsPlay){
                this.pausesong()
            }else{
                this.playersong()
                
            }
        },
        pausesong(){
            // 暂停
            this.$refs.audioref.pause()
            this.IsPlay = false
        },
        playersong(){
            //播放
            this.$refs.audioref.play()
            this.IsPlay = true
        },
        oninputevent(){
            // 处理input【range】滑动事件
            this.$refs.timerange.oninput = ()=>{
                this.$refs.audioref.currentTime = this.$refs.timerange.value
            }
        },
        timestar(){
            // 处理歌曲播放时间 
            this.$refs.audioref.ontimeupdate = ()=>{//播放位置事件改变事件
                let x = parseInt(this.$refs.audioref.currentTime/60)
                let y = parseInt(this.$refs.audioref.currentTime - (x*60))
                this.playermessage.timestar = `${x<10?('0'+x):x}:${y<10?('0'+y):y}`
                this.$refs.timerange.value = parseInt(this.$refs.audioref.currentTime)
            } 
        },
        timeend(){
            // 处理歌曲时间
            let x = parseInt(this.$refs.audioref.duration/60)
            let y = parseInt(this.$refs.audioref.duration - (x*60))
            this.playermessage.timeend = `${x<10?('0'+x):x}:${y<10?('0'+y):y}`
            this.playermessage.max = parseInt(this.$refs.audioref.duration)
            if(!x.isNaN||!y.isNaN||x===""||y===""){
                setTimeout(() => {
                    this.timeend()
                }, 1000);
            }
        },
        disposelyric(a){
            // 处理歌词，
            // console.log(a)
            let t = a.split("\n")
            let arr = []
            let lryarr = []
             t.forEach(item => {
                arr.push(item.substring(item.indexOf("]")+1,item.length))
                this.playermessage.arr = arr
                this.timeend()
            });     
            this.playermessage.lryarr = lryarr
            
        },
        songplayer(value,ids){
            // 获取id，播放歌曲并处理
            this.playermessage.id = ids
            let _this = this
            axios.get(`https://autumnfish.cn/song/url?id=${value}`).then(function (response) {
                    _this.playermessage.url = response.data.data[0].url
                    
                }).catch(function (error) {
                    console.log(error);
            });
            axios.get(`https://autumnfish.cn/song/detail?ids=${value}`).then(function (response) {
                // console.log(response.data.songs);
                _this.playermessage.song = response.data.songs[0].name
                _this.playermessage.singer = response.data.songs[0].ar[0].name
                _this.playermessage.imgurl = response.data.songs[0].al.picUrl
                _this.playermessage.album = response.data.songs[0].al.name
                    }).catch(function (error) {
                        console.log(error);
            });
            axios.get(`https://autumnfish.cn/lyric?id=${value}`).then(function (response) {
                 _this.playermessage.lryic = response.data.lrc.lyric
                 _this.disposelyric(_this.playermessage.lryic)
            }).catch((error)=>{
                console.log(error);
                _this.disposelyric("没有歌词oooo")
            })
            _this.$refs.audioref.onloadedmetadata = ()=>{
                _this.playersong()
            }
            
        },
        plistevent(){
            this.songlistshow = !this.songlistshow
        },
        lrcdata(){
            //获取歌词
            axios.get('https://autumnfish.cn/lyric?id=33894312').then(function (response) {
                    console.log(response);
                }).catch(function (error) {
                    console.log(error);
            });
        },
        detailssong(){
            // 获取歌曲详情 ar[id].name 歌手  al.name专辑
            let _this = this
            axios.get(`https://autumnfish.cn/song/detail?ids=${_this.songlist}`).then(async function (response) {  
                    for (let index = 0; index < response.data.songs.length; index++) {
                        const element = response.data.songs[index];
                        _this.detail.push(element)
                    }

                }).catch(function (error) {
                    console.log(error);
            });
        },
        songlistdata(){
            // 获取歌单
            let _this = this
            axios.get('https://autumnfish.cn/playlist/detail?id=575022767').then(function (response) {
                    let a = response.data.playlist.trackIds
                    for (let index = 0; index < a.length; index++) {
                        const element = a[index].id;
                        _this.songlist.push(element)
                    }
                    // console.log(_this.songlist)
                    _this.detailssong()
                }).catch(function (error) {
                    console.log(error);
            });
        },
        songdata(){
            //获取歌曲链接
            axios.get('https://autumnfish.cn/song/url?id=4285915793').then(function (response) {
                    console.log(response);
                }).catch(function (error) {
                    console.log(error);
            });
        },
        serchclick(value){
            let _this = this
            if(value === '1'){
                _this.searchshow = true
            }else{
                _this.searchshow = false
            }
        },
        lertfun(vaule){
            switch (vaule) {
                case "underdevelopment":
                    this.lertevent("博主提示","暂未开发噢，不会吧，不会吧")
                    break;
                case "plist":
                    this.plistevent()
                    break;
                case "player":
                    this.songplayer()
                    break;
                default:
                    break;
            }
        },
        lertevent(a,b){
            this.hint = {
                title:a,
                content:b,
                lertboole:true
            }
            // this.lertbloor = true
        },
    },
}
</script>

<style lang="less">
#music{
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    .musicbody{
        position: relative;
        margin: 100px auto;
        width: 1000px;
        height: 672px;
        box-shadow: 0 0 10px 3px #292726;
        transition: all .5s;
        animation: mymusic .5s;
        background: rgba(0, 0, 0, 0.9);
        
        .musicheader{
            // 头部开始
            height: 100%;
            border-bottom: 1.5px solid rgba(204, 30, 30, 0.3);
            height: 40px;
            display: flex;
            align-items: center;
            // justify-content: center;
            cite{
                font-family: cursive;
                font-style: unset;
                color: #fff;
                text-indent: 5px;
            }
            div{
                margin-left: 10px;
            }
            div:nth-child(2){
                    span{border: 1px solid #3e2118;
                    padding: 0 3px;
                    font-weight: 900;
                    cursor: pointer;}
                    span:first-child{
                        border-right: none;
                    }
            }
            span{
                color: #afad76;
            }
            div:nth-child(3){
                position: relative;
                .lertsearch{
                    z-index: 5;
                    // 搜索
                    width: 420px;
                    position: absolute;
                    background: rgba(2, 33, 32, 0.97);
                    top: 32px;
                    left: -20px;
                    text-align: left;
                    div{margin: 0;}
                    div:first-child{
                        width: 0;
                        height: 0;
                        border-width: 0 7px 7px;
                        border-style: solid;
                        border-color: transparent transparent #011f1f;
                        position: absolute;
                        top: -7px;
                        left: 10px;
                    }
                    div:nth-child(2){
                        height: 500px;
                        overflow-y: scroll;
                        ul{
                            list-style-type: none;
                            box-sizing: border-box;
                            color: #fff;
                            li{
                                padding: 5px 0;
                                font-size: 12px;
                                text-indent: 10px;
                                cursor: pointer;
                                line-height: 20px;
                            }
                            li:hover{
                                background: sienna;
                            }
                        }
                    }
                }
                input{
                    background: rgba(0, 0, 0, 0.3);
                    border: none;
                    outline: none;
                    border-radius: 20px;
                    padding: 2px 0;
                    text-indent: 10px;
                    color: #fff;
                    box-shadow: 0 0 6px 2px #424138;
                }
                span{
                    position: absolute;
                    right: 5px;
                    cursor: pointer;
                }
            }
            div:nth-child(4){
                display: flex;
                align-items: center;
                img{
                    border-radius: 50%;
                    width: 20px;height: 20px;
                }
                span{
                    margin: 10px 1px 7px 5px;
                    font-size: 10px;
                    color: #b3aeae;
                }
                cite{
                    font-size:14px;
                }
            }
            div:nth-child(5){
                display: flex;
                justify-content: center;
                align-items: center;
                span{
                    margin: 0 15px;
                    font-size: 20px;
                }
                span:nth-child(2){
                    margin-top: -3px;
                }
            }
            div:nth-child(6){
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: auto;
                span{
                    margin: 0 15px;
                    font-size: 20px;
                    cursor: pointer;
                }
            }
            // 头部结束
        }
        .player{
            // 中间部分
                display: flex;
                width: 100%;
                height: 450px;
                .left{
                    width: 45%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                    position: relative;
                    div:first-child{
                        width: 315px;
                        height: 315px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        img{
                            border-radius: 50%;
                            width: 150px;
                            z-index: 0;
                            height: 150px;
                            transition: all .5s;
                            box-shadow: 0 0 0 5px #000000f7, 0 0 0 30px #021013f7, 0 0 0 40px #103a3af7, 0 0 50px 30px #26494ef7, 0 0 0 40px #0a1f1ff7, 0 0 60px 39px #26494ef7, 0 0 70px 60px #030a0af7, 0 0 0px 70px #2b3131f7
                        }
                        .imganimtion{
                            animation: imgfun 20s infinite linear;
                            animation-play-state:paused;
                        }
                        .imganimtionstate{
                             animation-play-state:running;
                        }
                       
                    }
                    div:last-child{
                        width: 100%;
                        position: absolute;
                        bottom: 0;
                        ul{
                            display: flex;
                            width: 100%;
                            list-style: none;
                            justify-content: center;
                        }
                        cite{
                            font-style: unset;
                            
                        }
                        li{
                            background-color: #072925;
                            line-height: 20px;
                            margin: 0 10px;
                            padding: 5px 5px;
                            color: #a27777;
                            cursor: pointer;
                        }
                    }
                }
                .right{
                    width: 55%;
                    box-sizing: border-box;
                    text-align: left;
                    h3{
                        width: 370px;
                    }
                    div{
                        overflow: hidden;
                        display: flex;
                        line-height: 20px;
                        span{
                            color: #758ec7;
                            width: 125px;
                            display: block;
                            overflow: hidden;
                            white-space: pre;
                            text-overflow: ellipsis;
                        }
                    }
                    ul{
                        list-style: none;
                        overflow-y: scroll;
                        height: 350px;
                        width: 390px;
                        border-right: 1px solid #4d5152;
                        li{
                            line-height: 40px;
                        }
                        .hightcolor{
                            color: red;
                        }
                    }
                    h3,ul{
                        margin: 20px 0;
                        color: #9bb1af;
                    }
                    ul::-webkit-scrollbar-track-piece{
                        width: 10px;
                        background: rgba(0, 0, 0, 1);
                    }
                    ul::-webkit-scrollbar-thumb{
                        border-radius: 10px;
                        //  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0);
                         background: #4d5152;
                    }
                }
            //中间结束
        }
        .footermusic{
            // 底部播放器开始
            height: 50px;
            width: 100%;
            position: absolute;
            bottom: 0;
            display: flex;
            color: #f4f4f5;
            background: rgb(25, 20, 20);
            // align-items: center;
            // justify-content: center;
            div{
                height: 100%;
                span{
                    cursor: pointer;
                }
            }
            div:first-child{
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: 20px;
                span{
                    margin: 0 10px;
                    border-radius:50%;
                    border: 2px solid #88828e;
                    width: 30px;
                    height: 30px;
                    line-height: 30px;
                    cursor: pointer;
                }
                span:nth-child(2){
                    font-size: 10px;
                }
            }
            div:nth-child(2){
                font-style: 12px;
                display: flex;
                justify-content: center;
                align-items: center;
                input[type="range"] {
                    /*-webkit-box-shadow: 0 1px 0 0px #424242, 0 1px 0 #060607 inset, 0px 2px 10px 0px black inset, 1px 0px 2px rgba(0, 0, 0, 0.4) inset, 0 0px 1px rgba(0, 0, 0, 0.6) inset;*/
                    -webkit-appearance: none; /*去除默认样式*/
                    // margin-top: 42px;
                    background-color: #2d343c;
                    border-radius: 15px;
                    width: 390px;
                    -webkit-appearance: none;
                    height:4px;
                    padding: 0;
                    border: none;
                    outline: none;
                    margin: 0 5px;
                    /*input的长度为80%，margin-left的长度为10%*/
                }
                input[type="range"]::-webkit-slider-thumb {
                    -webkit-appearance: none;/*去除默认样式*/
                    cursor: default;
                    top: 0;
                    height: 20px;
                    width: 20px;
                    transform: translateY(0px);
                    /*background: none repeat scroll 0 0 #5891f5;*/
                    background: #fff;
                    border-radius: 15px;
                    border: 5px solid #006eb3;
                    /*-webkit-box-shadow: 0 -1px 1px #fc7701 inset;*/
                }
            }
            div:nth-child(3){
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: 20px;
                input[type="range"] {
                    /*-webkit-box-shadow: 0 1px 0 0px #424242, 0 1px 0 #060607 inset, 0px 2px 10px 0px black inset, 1px 0px 2px rgba(0, 0, 0, 0.4) inset, 0 0px 1px rgba(0, 0, 0, 0.6) inset;*/
                    -webkit-appearance: none; /*去除默认样式*/
                    // margin-top: 42px;
                    background-color: #2d343c;
                    border-radius: 15px;
                    width: 95px;
                    -webkit-appearance: none;
                    height:4px;
                    padding: 0;
                    border: none;
                    outline: none;
                    margin: 0 5px;
                    /*input的长度为80%，margin-left的长度为10%*/
                }
                input[type="range"]::-webkit-slider-thumb {
                    -webkit-appearance: none;/*去除默认样式*/
                    cursor: default;
                    top: 0;
                    height: 20px;
                    width: 20px;
                    transform: translateY(0px);
                    /*background: none repeat scroll 0 0 #5891f5;*/
                    background: #fff;
                    border-radius: 15px;
                    border: 5px solid #006eb3;
                    /*-webkit-box-shadow: 0 -1px 1px #fc7701 inset;*/
                }
            }
            div:nth-child(4){
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: auto;
                span{
                    margin: 0 10px;
                }
                .songlist{
                    position: absolute;
                    width: 450px;
                    height: 450px;
                    bottom: 50px;
                    right: 0;
                    background: rgba(21, 22, 23, 0.9);
                    text-indent: 10px;
                    ul{
                        text-align: left;
                        list-style: none;
                        width: 100%;height: 100%;
                        overflow-y: scroll;
                        li{
                            height: 20px;
                            font-size: 12px;
                            padding: 6px 0;
                            background: rgb(3, 29, 41);
                            cursor: pointer;
                            overflow: hidden;
                            white-space: pre;
                            text-overflow: ellipsis;
                        }
                        li:nth-child(2n-1){
                            background: #04161f;
                        }
                    }
                    
                }
            }
            //底部播放器结束
        }
    }
}
@keyframes mymusic {
    to{
        transform: scale(1);
    }
    from{
        transform: scale(0);
    }
}
@keyframes imgfun {
    to{
        transform: rotateZ(360deg);
    }
    from{
        transform: rotateZ(0);
    }
}
</style>