<template>
	<div id="Login">
		<div class="login">
			<h2 class="headline">LOGIN</h2>
			<form @submit.prevent="onSubmit">
				<div class="loginstyle">
					<input type="text" ref="inorymodel" required />
					<label>Username</label>
				</div>

				<div class="loginstyle">
					<input type="text" ref="inorycode" required />
					<label>Password</label>
				</div>
				<button class="btnstyle" @click="verification()">submit
					<span></span>
					<span></span>
					<span></span>
					<span></span>
				</button>
			</form>
	
		</div>
		<lert-module :hint="hint"></lert-module>
	</div>
</template>
<script>
import  LertModule from "@/components/lertmodule/lertmodule.vue";
export default {
  name: "Login",
  data() {
	  return {
		Identification:"inory",
		code:"123456",
		inorymodel:"",
		inorycode:"",
		hint:{
			title:`温馨提示`,
			content:`调用错误！你是不是搞了什么坏东西`,
			lertboole:false
		}
	  }
  },
  methods: {
	verification(){
		this.inorymodel = this.$refs.inorymodel.value
		this.inorycode = this.$refs.inorycode.value
		if(this.Identification===this.inorymodel&&this.inorycode===this.code){
			this.hint = {
                title:`温馨提示`,
                content:`lalala~~~`,
				lertboole:true,		
			}
			setTimeout(() => {
				this.$router.push({path:"/editor"})
			}, 1000);
		}else{
			this.hint = {
                title:"温馨提示",
                content:`你不对劲！`,
                lertboole:true
			}	
		}
	},
	onSubmit(){
		 return false;
	},	  
  },
  components:{
	  LertModule
  }
};
</script>
<style lang="less" scoped>
	input,
	button {
		background: transparent;
		/* 颜色透明 */
		border: none;
		outline: none;
	}

	#Login {
		// background: url(../WebsitePicture/135.jpg) no-repeat 100%;
		perspective: 1000px;
		background-color: #ffffff;
		font-size: 16px;
		font-family: -webkit-pictograph;
		color: #03e9f4;
		height: 100vh;
		width: 100%;
		position: fixed;
		top: 0;
		z-index: 9;
		/* 高度是整个屏幕 */
		/* background: linear-gradient(#141e30,#243b55); */
		display: flex;
		/* 弹性盒子 */
		justify-content: center;
		align-items: center;
		 background: rgba(1,1,1,0.4);

		.headline {
			text-align: center;
			color: #ffffff;
		}

		.login {
			width: 500px;
			height: 320px;
			background-color: rgba(0, 0, 0, 0.5);
			margin: 10% auto;
			box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.6);
			padding: 40px;
			box-sizing: border-box;

			.loginstyle {
				height: 45px;
				border-bottom: 1px solid #fff;
				margin-bottom: 40px;
				position: relative;

				input {
					width: 100%;
					height: 100%;
					color: #FFFFFF;
					padding-top: 20px;
					box-sizing: border-box;
				}

				label {
					position: absolute;
					left: 0;
					top: 12px;
					transition: all 0.5s linear;
				}

				input:focus+label,
				input:valid+label {
					/* 获取焦点 附近的元素 获得焦点的时候固定*/
					top: 0ex;
					font-size: 12px;
				}
			}
		}
		.btnstyle {
			padding: 10px 20px;
			margin-top: 30px;
			overflow: hidden;
			color: #03E9F4;
			position: relative;
			text-transform: uppercase;
			letter-spacing: 2px;
		}

		.btnstyle:hover {
			border-radius: 5px;
			color: #fff;
			box-shadow: 0 0 5px 0 #03E9F4,
				0 0 25px 0 #03E9F4,
				0 0 50px 0 #03E9F4,
				0 0 100px 0 #03E9F4;
			background: #03E9F4;
		}

		.btnstyle>span {
			position: absolute;
		}

		.btnstyle>span:nth-child(1) {
			width: 100%;
			height: 2px;
			background: -webkit-linear-gradient(left, transparent, #03e9f4);
			left: -100%;
			top: 0px;
			animation: line1 1s linear infinite;
			/* 无限循环 */
		}

		@keyframes line1 {

			50%,
			100% {
				left: 100%;
			}
		}

		.btnstyle>span:nth-child(2) {
			width: 2px;
			height: 100%;
			background: -webkit-linear-gradient(top, transparent, #03e9f4);
			right: 0;
			top: -100%;
			animation: line2 0.25s linear infinite;
		}

		@keyframes line2 {

			50%,
			100% {
				top: 100%;
			}
		}

		.btnstyle>span:nth-child(3) {
			width: 100%;
			height: 2px;
			background: -webkit-linear-gradient(left, #03e9f4, transparent);
			left: 100%;
			bottom: 0;
			animation: line3 0.5s linear infinite;
		}

		@keyframes line3 {

			50%,
			100% {
				left: -100%;
			}

		}

		.btnstyle>span:nth-child(4) {
			width: 2px;
			height: 100%;
			background: -webkit-linear-gradient(top, #03e9f4, transparent);
			left: 0;
			top: 100%;
			animation: line4 0.75s linear infinite;
		}

		@keyframes line4 {

			50%,
			100% {
				top: -100%;
			}
		}
	}
</style>
