<template>
    <div id="editor">
        
       <div ref="editorElem" class="editor"></div>
       <br>
        <div class="infoe">        
           <input type="text" ref="title" placeholder="标题">
           <input type="text" ref="time" placeholder="时间">
           <input type="text" ref="author" placeholder="作者">
           <input type="text" ref="id" placeholder="ID">
           <!-- <input type="file" ref="fileE" multiple="multiple" accept=".png,.jpeg,.jpg"> -->
            <input type="text" ref="fileE">
           <button @click="jsonevent">GO</button>
       </div>
       
    </div>
</template>

<script>
import E from "wangeditor";

export default {
    name:"Editor",
    data() {
        return {
            editorval:{
                title:"",   
                content:"", 
                author:"",
                timer:"",
                id:"",
                imgurl:""
            } 
        }
    },
    mounted() {
        this.editor = new E(this.$refs.editorElem);// vue挂载完后,实例化对象绑定div
        this.editor.create();//创建编辑器
    },
    methods: {
        jsonevent(){
            this.editorval = {
                title:this.$refs.title.value,
                content:this.editor.$textElem[0].innerHTML,
                author:this.$refs.author.value||`inory`,
                time:this.$refs.time.value || this.gettime,
                id:this.$refs.id.value,
                // imgurl:`data/${this.$refs.fileE.files[0].name}`
                imgurl:this.$refs.fileE.value
            }
            this.fsenvet()
        },
        fsenvet(){
            console.log()
            let FileSaver = require('./js/FileSaver.min.js'); 
            let content = JSON.stringify(this.editorval);
            let blob = new Blob([content ], {type: "text/plain;charset=utf-8"}); 
            console.log(FileSaver,content,blob)
            FileSaver.saveAs(blob, "inory.json");
        }
    },
    computed: {
        gettime: function(){
            let getnewtime = new Date();
            let gettimer  = `${getnewtime.getFullYear()}-${getnewtime.getMonth()+1}-${getnewtime.getDate()}`
            return gettimer
        }
    },
}
</script>

<style lang="less">
    #editor{
        .infoe{
            color: chartreuse;
            padding: 10px;
            position: relative;
            max-width: 1000px;
            margin: 0 auto;
            display: flex;
            flex-wrap: wrap;
            input{
                border: none;
                background: rgba(0, 0, 0, 0.5);
                color: #fff;
                border-radius: 10px;
                outline: none;
                box-shadow: 0 0 5px 6px #18a2e4;
                max-width: 170px;
                height: 35px;
                text-indent: 20px;
                margin: 10px;
            }
        }
        .editor{
            margin: 0 auto;
            max-width: 1000px;
            background: rgba(255, 255, 255, 0.5);
            text-align: left;
        }
        button{
            border-radius:10px ;
            border: none;
            outline: none;
            padding: 20px;
            background: rgba(100, 100, 100, 0.5);
            cursor: pointer;
            font-size: 16px;
            color: springgreen;
            
            position:absolute;
            right:10px;
            
        }
        button:hover{
            box-shadow: 0 0 20px 5px #0cdc56;
            
        }
    }
</style>