<template>
  <div id="lertmodule" v-if="hint.lertboole">
      <div v-bind:class="{btmscale:isbtmscale}">
          <!-- 弹框组件 -->
          <h2>{{hint.title}}</h2>
          <p>{{hint.content}}</p>
          <button @click="lertevent">确定</button>
      </div>
  </div>
</template>

<script>
export default {
    name:"lert-module",
    props:['hint'],
    data() {
        return {
            isbtmscale:false
        }
    },
    methods: {
        lertevent(){
            this.isbtmscale = true
            setTimeout(() => {
                this.hint.lertboole = false
                this.isbtmscale = false
            }, 500);
        }
    },
}
</script>
<style lang="less">
    #lertmodule{
        position: fixed;
        left: 0;
        top: 0;
        color: #fff;
        width: 100%;
        background:rgba(0, 0, 0, 0.5);
        color: #fff;
        height: 100vh;
        padding: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: normal;
        z-index: 999;
        div{
            width: 300px;
            transition: all .5s;
            background: rgba(0, 0, 0, 0.5);
            border-radius: 10px;
            animation: lertfun .5s;
            z-index: 10;
            box-shadow: 0 0 6px 3px #15dcf1;
            h2{
                padding: 10px;
            }
            p{
                padding: 0 10px 10px 10px;
                text-align: center;
                word-break: break-all;
            }
            button{
                border: none;
                background: rgba(0, 0, 0, 0.5);
                color: #fff;
                cursor: pointer;
                font-size: 16px;
                width: 100%;
                padding: 10px 0;
                outline: none;
            }
        }
        .btmscale{
            transform: scale(0);
        }
    }
    @keyframes lertfun {
        to{
            transform: scale(1);
        }
        from{
            transform: scale(0);
        }
    }
</style>