import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import NProgress from 'nprogress' // *进度条
import 'nprogress/nprogress.css' // *进度条
// import lertobj from "./components/lertmodule/index"
Vue.config.productionTip = false;
// Vue.use(lertobj)
NProgress.inc(0.2)// *进度条
NProgress.configure({ easing: 'ease', speed: 500, showSpinner: false })// *进度条
router.beforeEach((to,from,next) => {// *进度条
  NProgress.start() 
  next()
})
router.afterEach(() => {// *进度条
  NProgress.done()
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
