<template>
  <div id="app">
    <load-module v-show="loadboole"></load-module>
    <div class="body" :style="{backgroundImage: 'url(' + imgdata.cardPath + ')' }">
      <!-- <canvas id="canvas" v-show="canvasshow"></canvas> -->
    </div>
    <div style="padding:10px 0">
      <img :src="imgdata.logo" height="70px" alt="" @click="tab">
    </div>
    <transition name="fade">
          <router-view />
    </transition>
    <Infoicon></Infoicon>
  </div>
</template>

<script>
import Infoicon from "@/components/Infoicon.vue";
// import "../public/canvas.js"
import axios from "axios"
import LoadModule from "@/components/Load/LoadModule.vue";
export default {
  name:"App",
  components:{
    Infoicon,
    LoadModule
  },
  data() {
    return {
      loadboole:true,
      canvasshow:false,
      nums:0,
      imgdata:{
        cardPath:"https://uploadstatic.mihoyo.com/contentweb/20200410/2020041019014847737.png",
        logo:require("../public/inory.png")
      }
    }
  },
  methods: {
    tab(){
      let _this = this
      let imgurl = "https://www.bh3.com/content/bh3Cn/getContentList?pageSize=9&pageNum=1&channelId=177";
      _this.nums += 1;
      axios.get(imgurl).then((res)=>{
        _this.imgdata = {
            cardPath:res.data.data.list[this.nums].ext[0].value[0].url,
            logo:require("../public/inory.png")
          }
      }).catch((err)=>{
        console.log(err)
        _this.nums = 0;
      })
    }
  },
  mounted() {
    window.onload = ()=>{
      console.log('加载完成')
      this.loadboole = false
    }
  },
  updated() {
    
  },
}

</script>

