<template>
<!-- 文章列表
    vue.runtime.esm.js?2b0e:619 [Vue warn]: Do not use built-in or reserved HTML elements as component id: article
 -->
    <div id="article">

        <ul>
            <li v-on:click="skippage(item.id)" v-for="(item,index) in mydata" :key="index">
                <div>
                    <img :src="item.imgurl" alt="XXX">
                </div>
                
                <div>
                    <span>{{item.title}} </span>
                </div>
                <div>
                    <span>{{item.time}}</span>
                    <span>评论（unopen）</span>
                    <span>浏览（unopen）</span>
                </div>
            </li>
            <li class="more">
                <div class="">
                    MORE
                </div>
            </li>
        </ul>
        
    </div>
</template>

<script>
// import { getarticle } from "../axiosapi/index";
// import axios from "axios";
import data from "../../public/data.json"
export default {
    name:"articlelist",
    data(){
        return{
            items:{}
        }
    },
    methods:{
        skippage:function(id){
            this.$router.push({
                path:`/detailpage/${id}`
            })
        },
        datajson:function(){
            this.items = data.data
        }
    },
    beforeMount(){
        this.datajson()

    },
    computed: {
        mydata:{
            get:function(){
                return this.items; 
            }
        }
    },
}
</script>
<style lang="less">
    #article{
        width: 100%;
        // margin-top: 70px;
        .more{
            display: flex;
            cursor: pointer;
            justify-content: center;
            align-items: center;
            color: #23d420;
            text-align: center;
        }
        ul{
            display: flex;
            flex-flow: wrap;
            justify-content: space-around;
            margin: 1% auto;
            max-width: 900px;
            background: rgba(246, 255, 255, 0.5);
            padding: 10px 5px;
        li{
            list-style: none;
            width: 280px;
            height: 230px;
            background: #fff;
            flex: 1,1,280px;
            margin-top: 20px;
            font-size: 12px;
            cursor: pointer;
            transition: all 0.1s;
            overflow: hidden;
            border-radius: 5px;
            box-shadow: 1px 2px 20px #000;
            div{
                width: 100%;
            }
            div:not(:first-child){
                line-height: 30px;
            }
            img{
                width: 100%;transition: all 1s;
                height: 170px;
            }
        }
        li:hover{
            box-shadow: 1px 1px 100px rgb(200, 216, 151);
            img{
                transform: scale(1.1);
            }
        }
        
    }}

</style>